<template>
  <div class="list">
    <div class="pageBanner" :style="'background:url('+pageBanner+') center no-repeat'"></div>
    <div class="pageNav">
      <h3>
        <span>{{ pageNavList.Name }}</span>
      </h3>
      <ul v-if="pageNavList.Clist">
        <li v-for="i in pageNavList.Clist" :key="i.ID">
          <hrefformat :url="i.Url" :param="{cid:i.ID}" :content="i.Name"></hrefformat>
        </li>
      </ul>
    </div>
    <div class="pageMain clearfix">
      <router-view :key="$route.fullPath" />
    </div>
  </div>
</template>

<script>
export default {
  name: "list",
  data() {
    return {
      msg: "yemian",
      pageCid: "",
      pageFCid: "",
      pageNavList: [],
      pageRouterList: [],
      pageBanner: require("../assets/img/pageBanner.jpg")
    };
  },
  mounted() {
    var that = this;
    that.pageRouterList = that.$route.query;
    if (that.pageRouterList.cid) {
      that.pageCid = that.pageRouterList.cid;
      that.navFindF();
    } else {
      that.pageCid = that.Base.GetNewsInfoByNid(that.pageRouterList.id).Fid;
      that.navFindF();
    }
    if (!(that.pageNavList.Logo == "http://base.hope55.com"))
      that.pageBanner = that.pageNavList.Logo;
  },
  methods: {
    Navformat: function(data) {
      var obj = data;
      // console.log(obj);
      var temp = [];
      for (var i = 0; i < obj.length; i++) {
        if (obj[i].FID == "0") {
          var main = obj[i];
          main.Clist = [];
          for (var j = 0; j < obj.length; j++) {
            if (obj[j].FID == main.ID) {
              main.Clist.push(obj[j]);
            }
          }
          temp.push(main);
        }
      }
      return temp;
    },
    navFindF: function(id) {
      var that = this;
      var navList = that.Navformat(that.Base.GetNavColumnsByDid());
      if (that.Base.GetColumnByid(that.pageCid).Fid) {
        that.pageFCid = that.Base.GetColumnByid(that.pageCid).Fid;
        for (let key in navList) {
          if (navList[key].ID == that.pageFCid) {
            that.pageNavList = navList[key];
          }
        }
      } else {
        for (let key in navList) {
          if (navList[key].ID == that.pageCid) {
            that.pageNavList = navList[key];
          }
        }
      }
    }
  }
};
</script>

<style scoped>
.list {
  width: 100%;
  overflow: hidden;
  padding-bottom: 40px;
}
.pageBanner {
  width: 100%;
  height: 300px;
}
.pageNav {
  width: 1200px;
  height: 50px;
  background-color: #f7f7fa;
  margin: 0 auto;
}
.pageNav h3 {
  float: left;
  width: 260px;
  height: 50px;
  line-height: 50px;
  text-indent: 82px;
  font-size: 24px;
  color: white;
  position: relative;
}
.pageNav h3::before {
  content: "";
  width: 220px;
  height: 70px;
  bottom: 0px;
  left: 0px;
  position: absolute;
  background-color: #4273ff;
}
.pageNav h3::after {
  content: "";
  width: 0px;
  height: 0px;
  bottom: 0px;
  right: 0px;
  position: absolute;
  border-left: solid 20px #4273ff;
  border-bottom: solid 35px #4273ff;
  border-right: solid 20px transparent;
  border-top: solid 35px transparent;
}
.pageNav h3 span {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 3;
}
.pageNav ul {
  float: right;
  width: 900px;
  display: flex;
}
.pageNav ul li {
  flex: 1;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pageNav ul li a {
  font-size: 18px;
  color: #333333;
  text-align: center;
  line-height: 1.2;
}
.pageNav ul li a.router-link-active,
.pageNav ul li:hover a {
  color: #4273ff;
}
.pageMain {
  width: 1200px;
  margin: 0 auto;
  margin-top: 50px;
  padding-top: 1px;
}
</style>



